import React from 'react';
import "../App.css";
import TekstfeltLeft from './TekstfeltLeft';
import TekstfeltRight from './TekstfeltRight';
import MidpagePicture from './MidpagePicture'
import figur1 from "../assets/figure_bin.png";
import figur2 from "../assets/bag_bin.png";
import figur3 from "../assets/bottle_pant.png";
import figur4 from "../assets/figure_bag.png";
import {Collapse} from 'react-collapse';


function MainBody() {
  return(
    <div>
        <div className="Tekstfelt_left">
          <TekstfeltLeft titel="1. Utlevering av utstyr" teaser="Beholder, poser og nødvendig informasjon." icon={figur1} />
        </div>
        <div className="Tekstfelt_right">
          <TekstfeltRight titel="2. Plasser pose i beholder" teaser="Benytt utlevert pose som er godkjent for pick-up." icon={figur2}/>
        </div>
        <MidpagePicture />
        <div className="Tekstfelt_left">
          <TekstfeltLeft titel="3. Putt pant i beholder" teaser="Tomme enheter som kan pantes i Norden." icon={figur3}/>
        </div>
        <div className="Tekstfelt_right">
          <TekstfeltRight titel="4. Full beholder?" teaser="Når beholderen er full, knyt posen og sett ut til henting." icon={figur4}/>
        </div>
    </div>

  );
}

export default MainBody;
