import React from 'react';
import "../App.css";
import Icon from './Icon';

function TekstfeltRight(props) {
  return(
    <div className="right">
      <div className="icon_body">
        <Icon icon={props.icon} />
      </div>
      <div class="Tekst_boks">
        <h3>{props.titel}</h3>
        <p>{props.teaser}</p>
      </div>
    </div>
  );
}
export default TekstfeltRight;
