import React from 'react';
import "../App.css";
import Icon from './Icon';
import {Collapse} from 'react-collapse';

function TekstfeltLeft(props) {
  return(
    <Collapse isOpened={true || false}>
    <div className="left">
      <div class="Tekst_boks">
        <h3>{props.titel}</h3>
        <p>{props.teaser}</p>
      </div>
      <div className="icon_body">
        <Icon icon={props.icon} />
      </div>
    </div>
    </Collapse>
  );
}
export default TekstfeltLeft;
